import { PureComponent } from "react";
import { Navbar } from "@blueprintjs/core";
import { Link } from "./Link";
import { UrlObject, format } from "url";
import classNames from "classnames";
import styles from "./Header.module.scss";

const prefix = process.env.BASE_PATH;

function HeaderLink(props: {
    text: string;
    href: string | UrlObject;
}): JSX.Element {
    const { text, href } = props;
    return (
        <Link href={href} as={prefix + format(props.href)} prefetch>
            <a className={styles["page-link"]}>{text}</a>
        </Link>
    );
}

export class Header extends PureComponent<{}> {
    render(): JSX.Element {
        return (
            <Navbar className={classNames("bp3-dark", styles["header"])}>
                <Navbar.Group>
                    <Navbar.Heading>
                        <HeaderLink href="/" text="Tiro" />
                    </Navbar.Heading>
                    <Navbar.Divider />
                    <HeaderLink href="/playground" text="Playground" />
                    <HeaderLink href="/reference" text="Reference" />
                    <HeaderLink href="/about" text="About" />
                </Navbar.Group>
            </Navbar>
        );
    }
}

export default Header;
